<template>
  <div style="height:100%">
    <template v-if="$store.state.customerNo !== '' ">
      <div class="operate-div" style="font-size: 0.94em;padding-right:0.8em">
        <selects ref="selects"  :option="selectOpt"   @getSelect="getOption"></selects>
         <selects ref="selects" v-if="showAllDevice" :option="selectDevice"   @getSelect="getDevice"></selects>
        <datePicker v-if="beforeDisable!==''" ref="timer"  type= "doubleDate"  :dateOpt="dateOpt" @getDatePicker="getOption" ></datePicker>
      <!--   <button class="button-operate"   @click="getSearchTable" style="margin-right:0;">
          <i class="hasIcon"  style="margin-top: 0;"><icon name="search"  style="font-size: 1.4em;"></icon></i>
          搜索
        </button> -->
      </div>
      <div class="areaChart" id="areaChart" ref="chart" ></div>
       
      <div class="list-header" style="border:0;float:left;padding-left:1em">
        抄表数据详情
      </div>
      <div class="all_foot_con3" style=" width: calc(100% - 2em);margin: 0 1em;">
        <table class="all_foot_ta" style="padding-right: 20px">
          <tr style="background: #305ea0">
            <td width="20%">时间</td>
            <td width="15%">表号</td>
            <td width="15%">总示数（<span class="title_dw">{{optObj.dw}}</span>）</td>
            <td width="17%">账户余额（元）</td>
            <td width="17%" v-if="localControl == 1">购{{optObj.name}}总金额（元）</td>
            <td width="16%" v-if="localControl == 1">购{{optObj.name}}次数</td>
          </tr>
        </table>
        <div class="overflow" style="width: calc(100% + 17px)">
          <VuePerfectScrollbar style="position: relative;height:100%;width:100%">
            <table class="all_foot_ta table  " >
              <tr v-for="(item, $index) in tableData" style="font-size: 0.875em">
                <td  width="20%">{{item.collectDate}}</td>
                <td  width="15%">{{item.deviceNo}}</td>
                <td  width="15%">{{item.pr0===null?'--':item.pr0}}</td>
                <td  width="17%">{{item.balance===null?'--':item.balance}}</td>
                <td  width="17%" v-if="localControl == 1">{{item.payMoney}}</td>
                <td  width="16%" v-if="localControl == 1">{{item.payCount}}</td>
              </tr>
            </table>
          </VuePerfectScrollbar>
        </div>
      </div>
    </template>
    <template v-else>
      <div class="emptyShowPic">
        <div class="emptyContent">
          <img src="dist/img/empty-data.png">
          <p>{{ '请先输入查询条件获取对应信息'}}</p>
        </div>
      </div>
    </template>
    <dialogs type="alert" ref="alerts"></dialogs>
  </div>
</template>
<script>
//  import echarts from 'echarts'
  export default{
    data() {
      return {
        beforeDisable: '',//时间选择器的起始时间
        tableHeight: '',
        searchDate: '',
        showAllDevice: true,
        localControl: 1, // 1本地费控 2远程费控 远程费控是隐藏购电总金额和购电次数
        searchParam: {
          deviceType: '07',
          startDate: '',
          endDate: '',
          cno: '',
          meterUserNo: ''
        },
        tableData: [],
        line: ''
      }
    },
    computed: {

       selectDevice: function () {
        return {
          cs: 'server',
          key: 'searchParam.deviceNo',
          url: 'comprehensive/queryDeviceInfo',
          method: 'post',
          param: {deviceType: this.searchParam.deviceType, customerNo: this.$store.state.customerNo},
          width: '12em',
          hasAll: false
        }
      },
      selectOpt: function () {
        return {
          cs: 'client',
          width: '8em',
          key: 'searchParam.deviceType',
          data: this.getEnumItem('deviceType').obj
        }
      },
      dateOpt: function () {
        let dates = this.initDate(true);
        return {
          key: 'searchDate',
          value: {startDate: dates.start, endDate: dates.end},
          width: '23em'
        }
      },
      optObj: function () {
          let param = {};
          switch (this.searchParam.deviceType) {
            case '07':
                param = {name: '电', dw: 'kwh', val: '07'};
                break;
            case '08':
              param = {name: '水', dw: '吨', val: '08'};
              break;
            case '09':
              param = {name: '气', dw: '立方', val: '09'};
              break;
          }
          return param
      }
    },
    methods: {
      getDevice(val) {
        if(val.val !== '' && typeof val.val !== 'undefined') {
           let arr = val.val.split(',');
           if(arr[1] !=  this.searchParam.cno) {
               this.searchParam.meterUserNo = arr[0];
               this.searchParam.cno = arr[1];
               this.getSearchTable()
           }
        } else {
          this.searchParam.cno = '';
          this.searchParam.meterUserNo = ''
        }
      },
      makeLine: function (x, y) {
        this.line = echarts.init(document.getElementById('areaChart'));
        let option = {
          legend: {
            show: false,
            itemWidth: 55,
            padding: [5, 20, 15, 10],
            x: 'right',
            top: 20,
            textStyle: {
              color: '#000'
            }
          },
          toolbox: {
            show: false,
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          grid: {
            left:'50',
            right: 15,
            top: 30,
            bottom: 30
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: x,
            axisLabel: {
              textStyle: {
                color: '#fff'
              },
              formatter: function (value, index) {
                if (value.length < 5) {
                  return value
                }
                return value.substr(5)
              }
            },
            splitLine: {
              lineStyle: {
                color: '#657A8C'
              }
            },
            axisTick: {
              show: true,
              lineStyle: {
                color: '#657A8C'
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#657A8C'
              }
            }
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%'],
            name: this.searchParam.deviceType === '07' ? '电量（kwh）' : (this.searchParam.deviceType === '08' ? '水量（T）' : '天然气（Nm3）'),
            nameTextStyle: {
              color: '#fff',
              fontSize: this.getFontSize()
            },
            axisLabel: {
              formatter: '{value}',
              textStyle: {
                color: '#fff'
              }
            },
            splitLine: {
              lineStyle: {
                color: '#657A8C'
              }
            },
            axisTick: {
              show: false,
              lineStyle: {
                color: '#657A8C'
              }
            },
            axisLine: {
              show: true,
              lineStyle: {
                color: '#657A8C'
              }
            }
          },
          tooltip: {
            trigger: 'axis',
            borderRadius: 500,
            backgroundColor: 'rgba(202,79,82,0.5)',
            padding: [25, 20],
            formatter: function (params) {
              return params[0].data + 'kwh';
            },
            textStyle: {
              color: '#fff',
              fontWeight: 600,
              fontSize: 16
            }
          },
          series: {
            name: '',
            type: 'line',
            smooth: false,
           // sampling: 'average',
            itemStyle: {
              normal: {
                color: '#CA4F52'
              }
            },
            areaStyle: {
              normal: {
                color: '#544252'
              }
            },
            symbol: 'circle',
            symbolSize: 15,
            data: y
          }
        };
        let that = this;
        that.$nextTick(function () {

          that.line.setOption(option);
        });
      },
      getFontSize: function () {
        let fontSize = 13;
        let width = window.screen.width;
        let currentWidth = document.body.clientWidth;
        if(width <= 1366 || currentWidth <= 1366) {
          fontSize = 11;
        } else if(width <= 1700 || currentWidth <= 1700) {
          fontSize = 12;
        } else  {
          fontSize = 13;
        }
        return fontSize
      },
      getSearchTable: function () {

        if(this.$store.state.customerNo !== '' ) {
           let that = this;
          let arr = this.searchParam;
          arr.customerNo = that.$store.state.customerNo;
          arr.pageNumber = 1;
          arr.pageSize = 999999;
          this.$nextTick(function () {
            that.showAllDevice = true;
            setTimeout(function () {
             if(that.searchParam.cno !== '') {
                 that.getRequest({
                  // url: 'dist/json/measureHList.json',
                  // method: 'get',
                  url: 'comprehensive/queryCollectDataList',
                  method: 'post',
                  param: arr,
                  success: function (data) {
                    let Data = data.data;
                    that.tableData = Data.listData;
                    if(Data.listData.length > 0 && Data.listData[0].localControl == 2){
                      that.localControl = 2
                    };
                    let len = Data.listData.length;
                    let xData = Data.xData || [], yData = Data.yData || [0,0,0,0];
                    // for(let i=len -1 ; i > -1; i--) {
                    //    let item = Data.listData[i]
                    //    yData.push(item.pr0);
                     //    xData.push(item.collectDate);
                    // }
                    that.makeLine(xData, yData);
                  }
               })
             } else {
                 that.tableData = [];
                that.makeLine([], [])
             }
              
            }, 100)
           
          });
          }
      },
       windowChange:function () {
         if (this.line !== '') {
            this.line.resize();
         }
      },

    },
    mounted: function () {
      let date = this.initDate(true);
      this.searchDate = date.start + '至' + date.end;
      let that = this;
      let now = new Date();
      now.setMonth(now.getMonth() - 3);
      let monthNow = now.getMonth();
      let day = now.getDate() > 9 ? now.getDate() : '0' + now.getDate();
      this.beforeDisable = now.getFullYear();
        if (monthNow === 0) {
          this.beforeDisable = this.beforeDisable + '-0' + (monthNow + 1) + '-' + day;
        } else {
        
          this.beforeDisable = this.beforeDisable + '-' + ((monthNow + 1) < 10 ? ('0' + (monthNow + 1)) : (monthNow + 1)) + '-' + day;
        }
      //this.getSearchTable();
      // window.onresize = function () {
      //   if (that.line !== '') {
      //       that.line.resize();
      //   }
      // }
    },
    watch: {
      'searchDate'(val, oldVal) {
        let arr = this.searchDate.split('至');
        this.searchParam.startDate = arr[0];
        this.searchParam.endDate = arr[1];
        let arrs1 = arr[0].split('-');
        let arrs2 = this.beforeDisable.split('-');
        if(parseInt(arrs2[0]+arrs2[1]+arrs2[2]) <= parseInt(arrs1[0]+arrs1[1]+arrs1[2])) {
           if(oldVal != '') {
           this.getSearchTable();
           }
        } else {
           this.$refs.alerts.show({msg: '只能查询一个季度的数据，请重新选择时间范围'});
           let date = this.initDate(true);
           this.$refs.timer.dateOpt.value = {startDate: date.start, endDate: date.end};
           this.$refs.timer.init();
           this.searchDate = date.start + '至' + date.end;
        }
      },
      'searchParam.cno'(val, oldVal) {
         if(val !== '' && val !== oldVal) {
          if((oldVal != '' && val.substr(0, 2) != oldVal.substr(0, 2)) || oldVal == '') {
              this.getSearchTable();
            }
         }  
      },
      'searchParam.deviceType'(val) {
        this.showAllDevice = false;
        let date = this.initDate(true);
        let allDate = date.start + '至' + date.end;
        if (allDate === this.searchDate) {
          this.getSearchTable();
        } else {
          this.$refs.timer.dateOpt.value = {startDate: date.start, endDate: date.end};
          this.$refs.timer.init();
          this.searchDate = allDate;
        }
      },
       '$store.state.customerNo': {
           handler(val, oldVal) {
             if(typeof val !== 'undefined' && val !== oldVal) {
               this.showAllDevice = false;
              this.getSearchTable();
             }
           }
        }
    }
  }
</script>

<style lang="scss">
  .areaChart{
    height:40%;
    width:100%;
    float:left;
  }
  .all_foot_con3 {
    border: 1px solid rgba(255,255,255,0.4);
    border-radius: 2px;
    position: relative;
    float: left;
    margin-bottom: 1.2em;
    overflow: hidden;
    height: calc(60% - 9em);
    width: 100%;
    .all_foot_ta {
      width: 100%;
      text-align: center;
      line-height: 1.4em;
      overflow: scroll;
      height: 1.4em;
      margin-bottom:0;
      margin-left: 0;
    }
    .all_foot_ta td {
      padding: 0.5em 0;
      border-right: 1px solid rgba(255,255,255,0.4);
      font-size:0.9em;
    }
    .all_foot_ta td:last-child{
      border-right:0
    }
    .all_foot_ta tr{
      border-bottom:1px solid rgba(255,255,255,0.4);
      padding: 5px 0
    }
  }
  .overflow {
    overflow: scroll;
    width: 100%;
    height: calc(100% - 33px);
    overflow-x: hidden;
  }
  .overflow .all_foot_ta tr{
    cursor:pointer;
  }
  .overflow .all_foot_ta tr:hover {
    background:#227fdf;
    color:white
  }
</style>



